const Manager = () => import('./views/Manager');
const Pancake = () => import('./views/swap/Pancake');
const Uniswap = () => import('./views/swap/Uniswap');
const Deploy = () => import('./views/Deploy');
const Error404 = () => import('./views/Error404');
const Dashboard = () => import('./views/Dashboard');

export const routes = [
    { path: '', component: Dashboard, name: 'Dashboard' },
    { path: '/deploy/:type', component: Deploy, name: 'Deploy' },
    { path: '/manager', component: Manager, name: 'Manager' },
    { path: '/swap/uniswap', component: Uniswap, name: 'Uniswap' },
    { path: '/swap/pancake', component: Pancake, name: 'Pancake' },
    { path: '/**', component: Error404, name: 'Error404' }
];
