<template>

    <v-app>
        
        <div v-if="$vuetify.theme.dark">
            <span class="dotl"></span>
            <span class="dotr"></span>
        </div>

        <v-main :class="$vuetify.theme.dark ? 'background-gradient-dark' : 'background-gradient-light'">
            <alert />

            <mobile-mm v-if="$isMobile() && !mm" />
            <div v-else>
                <navigation />
                <transition name="page-transition" mode="out-in">
                    <router-view :key="$route.fullPath" class="router-view-custom" />
                </transition>
            </div>
            
            <contact />
        </v-main>
        
    </v-app>

</template>

<script>

    import Navigation from '@/components/Navigation';
    import { mapActions, mapState, mapMutations } from 'vuex';
    const Alert = () => import('@/components/Alert');
    const Contact = () => import('@/components/Contact');
    const MobileMetaMask = () => import('@/views/MobileMetaMask');

    export default {
        name: 'App',

        components: {
            'navigation': Navigation,
            'alert': Alert,
            'contact': Contact,
            'mobile-mm': MobileMetaMask
        },

        computed: {
            ...mapState({
                'w3': state => state.chain.w3,
                'wallet': state => state.chain.wallet,
                'mm': state => state.chain.mm
            })
        },

        metaInfo: {
            title: 'Dapp Tools',
            titleTemplate: '%s | Blockchain made easy',
            meta: [
                { name: 'description', content: 'Deploy and manage tokens and other blockchain smart contracts in a few simple clicks. No coding skills required.' },
                { name: 'og:title', content: 'Dapp Tools | Blockchain made easy' },
                { name: 'og:url', content: 'https://dapptools.tk' },
                { name: 'og:type', content: 'website' },
                { name: 'og:description', content: 'Deploy and manage tokens and other blockchain smart contracts in a few simple clicks. No coding skills required.' },
                { name: 'og:image', content: 'https://dapptools.tk/res/logo.png' },
                { name: 'og:image:secure_url', content: 'https://dapptools.tk/res/logo.png' },
                { name: 'og:image:type', content: 'image/png' },
                { name: 'og:image:width', content: '533' },
                { name: 'og:image:height', content: '396' },
                { name: 'og:image:alt', content: 'Dapp logo' }
            ]
        },

        watch: {
            wallet() {
                this.getUserContracts();
            }
        },

        methods: {
            ...mapActions({
                connectChain: 'chain/connectChain',
                setWallet: 'chain/setWallet',
                removeWallet: 'chain/removeWallet',
                displayAlert: 'alert/displayAlert',
                connectDB: 'db/connectDB',
                unsubscribeDBListeners: 'db/unsubscribeDBListeners',
                getUserContracts: 'db/getUserContracts',
                getDescriptions: 'db/getDescriptions'
            }),

            ...mapMutations({
                setChain: 'chain/setChain'
            }),

            handleAccountChanged(accounts) {
                if (accounts.length) {
                    this.setWallet(accounts[0]);
                } else {
                    this.removeWallet();
                }
            },

            handleNetworkChanged(id) {
                this.setChain(parseInt(id));
            },

            sleep(ms) {
                return new Promise(resolve => setTimeout(resolve, ms));
            }
        },

        async mounted() {
            this.connectChain();
            this.connectDB();
            this.getUserContracts();
            this.getDescriptions();

            while (!this.w3) {
                await this.sleep(50);
            }
            
            if (localStorage.wallet) {
                this.w3.eth.getAccounts().then( res => {
                    if (res.length) {
                        this.setWallet(res[0]);
                    }
                }).catch( err => {
                    this.displayAlert({ message: err.message, type: 'error' })
                });
            }

            if (localStorage.getItem('darkTheme') === 'false') {
                this.$vuetify.theme.dark = false;
            }

            if (window.ethereum) {
                window.ethereum.on('accountsChanged', this.handleAccountChanged);
                window.ethereum.on('chainChanged', this.handleNetworkChanged);
            }

            window.addEventListener('beforeunload', () => {
                if (window.ethereum) {
                    window.ethereum.removeListener('accountsChanged', this.handleAccountChanged);
                    window.ethereum.removeListener('chainChanged', this.handleNetworkChanged);
                }

                this.unsubscribeDBListeners();
            });
        }

    }

</script>

