        background: linear-gradient(180deg, #000000 0%, var(--v-info-base) 700%);
    }

    .background-gradient-light {
        background: linear-gradient(180deg, #ffffff 0%, var(--v-info-base) 700%);
    }

    .router-view-custom {
        padding: 10px;
    }

    .container {
        max-width: 100% !important;
    }

    .v-application * :not(.v-icon, .brand-text, h1, h2, h3, h4, h5, h6) {
        font-family: "karla" !important;
    }

    html {
        overflow-y: auto;
    }

    .page-transition-leave-to,
    .page-transition-enter {
        opacity: 0;
        transform: scale(1.1);
    }

    .page-transition-enter-active,
    .page-transition-leave-active {
        transition: all 0.2s ease;
    }

    .dotl {
        position: absolute;
        left: 20%;
