        height: 50%;
        width: 50%;
        animation: pulse 5s ease-in-out infinite;
        background-color: var(--v-primary-base);
        opacity: 0.35;
        border-radius: 50%;
        display: inline-block;
        filter: blur(150px);
        transform: translateZ(0);
    }

    .dotr {
        position: absolute;
        left: 35%;
        top: 25%;
        height: 50%;
        width: 50%;
        animation: pulse 5s ease-in-out infinite;
        background-color: var(--v-info-base);
        opacity: 0.35;
        border-radius: 50%;
        display: inline-block;
        filter: blur(150px);
        transform: translateZ(0);
    }

    @keyframes pulse {
        0%   { height: 47%; width: 47%; }
        50%  { height: 53%; width: 53%; }
        100% { height: 47%; width: 47%; }
    }

    .v-card, .v-dialog {
        box-shadow: 0 0 20px 5px #4caf5045 !important;
        border-radius: 10px !important;
    }

    .v-dialog .v-card {
        box-shadow: 0 0 0 0 #000000 !important;
    }

    ::-webkit-scrollbar {
        width: 7px;
        height: 7px;
    }

    ::-webkit-scrollbar-thumb {
        background: var(--v-primary-base);
        border-radius: 10px;
        border: 2px solid transparent;
        background-clip: padding-box;
    }

    ::-webkit-scrollbar-thumb:hover{
        background: var(--v-primary-darken2);
        border: 0;
    }

    ::-webkit-scrollbar-thumb:active{
        background: var(--v-primary-darken4);
        border: 0;
    }

    *:not(html) ::-webkit-scrollbar-track {
        background: transparent;
    }

    ::-webkit-scrollbar-track {
        background: #000000;
    }

    * {
        scrollbar-color: var(--v-primary-base) #000000;
    }

    *:not(html) {
        scrollbar-color: var(--v-primary-base) transparent;
    }

    .v-dialog {
        max-width: fit-content;
        text-align: center;
    }

    .v-textarea textarea {
