import Web3 from 'web3';
import { endpoints } from '@/store/constants';
import store from '@/store/index';


const state = () => ({
    wallet: null,
    currentChain: null,
    w3: null,
    mm: false,
    balance: ''
})

const actions = {
    connectWallet({ commit, dispatch }) {
        window.ethereum.request({ method:'eth_requestAccounts' }).then( res => {
            if (!res[0]) return;

            const v = res[0].toLowerCase();
            commit('setWallet', v);
            localStorage.wallet = v;
            dispatch('getNativeBalance');
        }).catch ( err => {
            store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
        });
    },

    setWallet({ commit, dispatch }, val) {
        const v = val.toLowerCase();
        commit('setWallet', v);
        localStorage.wallet = v;
        dispatch('getNativeBalance');
    },

    manageWallets({ commit, dispatch }) {
        window.ethereum.request({ method:'wallet_requestPermissions', params:[{ eth_accounts: {} }] }).then( _ => {
            window.ethereum.request({ method:'eth_requestAccounts' }).then( res => {
                if (!res[0]) return;
                
                const v = res[0].toLowerCase();
                commit('setWallet', v);
                localStorage.wallet = v;
                dispatch('getNativeBalance');
            }).catch ( err => {
                store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
            });
        }).catch ( err => {
            store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
        });
    },

    removeWallet({ commit }) {
        commit('unsetWallet');
        localStorage.removeItem('wallet');
    },

    async connectChain({ commit, dispatch }) {
        if (!window.ethereum) {
            setTimeout( () => store.dispatch('alert/displayAlert', { message: 'Non-Ethereum browser detected. You should consider trying MetaMask!' }, { root: true }), 2000);
        } else {
            commit('setMM');
        }

        try {
            let w3 = new Web3(window.ethereum || endpoints[0].url);
            commit('setWeb3', w3);
            let currentChainId = await w3.eth.getChainId();
            commit('setChain', currentChainId);
            dispatch('getNativeBalance');
        } catch (err) {
            store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
        }
    },

    getNativeBalance({ commit, state }) {
        if (state.wallet) {
            state.w3.eth.getBalance(state.wallet)
                .then( res => {
                    const bal = state.w3.utils.fromWei(res, 'ether');
                    commit('setBalance', bal);
                }).catch ( err => {
                    store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
                });
        }
    }
}

const mutations = {
    setWeb3 (state, w3) {
        state.w3 = w3;
    },

    setWallet(state, val) {
        state.wallet = val;
    },

    unsetWallet(state) {
        state.wallet = null;
    },

    setChain(state, chain) {
        state.currentChain = chain;
    },

    setMM(state) {
        state.mm = true;
    },

    setBalance(state, bal) {
        state.balance = bal;
    }
}

export default {
    namespaced: true,
    state,
    actions,
    mutations
}
