
const state = () => ({
    lastIndex: 0,
    indexes: [],
    messages: [],
    types: [],
    timeouts: [],
    dismissibles: []
})

const actions = {
    displayAlert({ commit }, payload) {
        commit('addMessage', payload.message);
        
        if (payload.type)
            commit('addType', payload.type);
        else
            commit('addType', 'info');
        
        if (payload.timeout)
            commit('addTimeout', payload.timeout * 1000);
        else
            commit('addTimeout', 5000);
        
        if (payload.dismissible)
            commit('addDismissible', payload.dismissible);
        else
            commit('addDismissible', false);
        
        commit('addIndex');
    },

    removeAlert({ commit, state }, val) {

        let i = state.indexes.findIndex( elem => { return elem === val } );

        commit('removeIndex', i);
        commit('removeMessage', i);
        commit('removeType', i);
        commit('removeTimeout', i);
        commit('removeDismissible', i);
    }
}

const mutations = {

    addIndex(state) {
        state.indexes.push(state.lastIndex);
        state.lastIndex += 1;
    },

    addMessage(state, val) {
        state.messages.push(val);
    },

    addType(state, val) {
        state.types.push(val);
    },

    addTimeout(state, val) {
        state.timeouts.push(val);
    },

    addDismissible(state, val) {
        state.dismissibles.push(val);
    },

    removeIndex(state, val) {
        state.indexes.splice(val, 1);
    },

    removeMessage(state, val) {
        state.messages.splice(val, 1);
    },

    removeType(state, val) {
        state.types.splice(val, 1);
    },

    removeTimeout(state, val) {
        state.timeouts.splice(val, 1);
    },
    
    removeDismissible(state, val) {
        state.dismissibles.splice(val, 1);
    }
}

export default {
    namespaced: true,
    state,
    actions,
    mutations
}