import secrets from '@/secrets.json';
import Vue from 'vue';
import { initializeApp } from 'firebase/app';
import { getFirestore, doc, collection, onSnapshot } from 'firebase/firestore';
import store from '@/store/index';


const sleep = (milliseconds) => new Promise((resolve) => setTimeout(resolve, milliseconds));

const state = () => ({
    db: null,
    descriptions: null,
    userContracts: null,
    unsubs: []
})

const actions = {
    connectDB({ commit }) {
        let project = null;
        if (process.env.NODE_ENV !== 'development') {
            project = initializeApp(secrets.firebaseConfig);
        } else {
            project = initializeApp(secrets.firebaseConfig_dev);
        }

        const db = getFirestore(project);

        commit('setDB', db);
    },

    async getDescriptions({ commit, state, dispatch }) {
        if (!state.db) {
            await sleep(500);
            dispatch('getDescriptions');

        } else {
            try {
                const unsub = onSnapshot(collection(state.db, "description"), snps => {
                    const data = [];
                    snps.forEach( doc => {
                        const obj = doc.data();
                        obj.name = doc.id;
                        data.push(obj)
                    });

                    commit('setDescriptions', data);
                });

                commit('addListenerUnsub', unsub);
            } catch (err) {
                store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
            }
        }
    },

    async getUserContracts({ commit, dispatch, state, rootState }) {

        while (!state.db) {
            await sleep(500);
        }
        
        try {
            if (!rootState.chain.wallet) {
                commit('setUserContracts', {});
                return;
            }

            const v = rootState.chain.wallet.toLowerCase()
            const unsub = onSnapshot(doc(state.db, "user_contracts", v), doc => {
                if (doc.exists()) {
                    commit('setUserContracts', doc.data());
                } else {
                    commit('setUserContracts', {});
                }
            });

            commit('addListenerUnsub', unsub);
        } catch (err) {
            store.dispatch('alert/displayAlert', { message: err.message, type: 'error' }, { root: true });
        }
    },

    addUserContract(ctx, data) {
        let url = '';
        if (process.env.NODE_ENV !== 'development') {
            url = 'https://api.dapptools.tk/user/contract/add';
        } else {
            url = 'https://daptools-back-2hrtjngy2-daptool.vercel.app/user/contract/add';
        }

        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }).catch( () => {
            store.dispatch('alert/displayAlert', { message: 'Database error while saving contract information.', type: 'error' }, { root: true });
        });
    },

    addLoadedUserContract({ commit }, data) {
        commit('addUserContract', {
            abi: data.address,
            address: data.address,
            chain: data.chain,
            constructorArguments: '',
            name: data.address,
            verified: true
        });
    },

    removeUserContract(ctx, data) {
        let url = '';
        if (process.env.NODE_ENV !== 'development') {
            url = 'https://api.dapptools.tk/user/contract/remove';
        } else {
            url = 'https://daptools-back-2hrtjngy2-daptool.vercel.app/user/contract/remove';
        }

        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        }).catch( () => {
            store.dispatch('alert/displayAlert', { message: 'Database error while removing contract information.', type: 'error' }, { root: true });
        });
    },

    renameUserContract({ dispatch }, data) {
        dispatch('addUserContract', data);
    },

    unsubscribeDBListeners({ commit }) {
        commit('unsubscribe');
    }
}

const mutations = {
    setDB(state, db) {
        state.db = db;
    },

    setDescriptions(state, data) {
        state.descriptions = data;
    },

    setUserContracts(state, contracts) {
        state.userContracts = contracts;
    },

    addUserContract(state, contract) {
        Vue.set(state.userContracts, contract.name, contract);
    },

    addListenerUnsub(state, unsub) {
        state.unsubs.push(unsub);
    },

    unsubscribe(state) {
        state.unsubs.forEach( unsub => unsub() );
        state.unsubs = [];
    }
}

export default {
    namespaced: true,
    state,
    actions,
    mutations
}
